import React, { useState, useEffect } from 'react';
import { Box, Paper, Typography, Button } from '@mui/material';

const ChatBubble = ({ message, isUser, reasoning, thinkingTime, finalized }) => {
  const [showReasoning, setShowReasoning] = useState(false);

  //  finalized 
  useEffect(() => {
    if (!finalized) {
      setShowReasoning(true);
    } else {
      setShowReasoning(false);
    }
  }, [finalized]);

  // 
  if (isUser) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', mb: 1 }}>
        <Paper
          elevation={1}
          sx={{
            p: 1,
            maxWidth: '80%',
            backgroundColor: 'primary.main',
            color: 'primary.contrastText',
            borderRadius: 2,
          }}
        >
          <Typography variant="body1">{message}</Typography>
        </Paper>
      </Box>
    );
  }

  //  AI 
  return (
    <Box sx={{ mb: 1 }}>
      {/* 
         AI  (finalized = false) 
        
      */}
      {!finalized && (
        <Typography
          variant="caption"
          color="text.secondary"
          sx={{ mb: 0.5, fontWeight: 'bold' }}
        >
          
        </Typography>
      )}

      {/* AI  */}
      <Box sx={{ display: 'flex', justifyContent: 'flex-start' }}>
        <Paper
          elevation={1}
          sx={{
            p: 1,
            maxWidth: '80%',
            backgroundColor: 'grey.300',
            color: 'black',
            borderRadius: 2,
          }}
        >
          {/*  finalized */}
          {finalized && thinkingTime && (
            <Typography
              variant="caption"
              color="text.secondary"
              sx={{ display: 'block', mb: 0.5 }}
            >
               {thinkingTime} 
            </Typography>
          )}

          {/*  finalized */}
          {finalized && (
            <Button
              onClick={() => setShowReasoning((prev) => !prev)}
              size="small"
              variant="text"
              sx={{ minWidth: 'auto', p: 0, mb: 0.5 }}
            >
              {showReasoning ? '' : ''}
            </Button>
          )}

          {/*  */}
          {(showReasoning || !finalized) && reasoning && reasoning.length > 0 && (
            <Box
              sx={{
                mt: 1,
                mb: 2,
                backgroundColor: 'grey.200',
                p: 1,
                borderRadius: 1,
              }}
            >
              <Typography
                variant="caption"
                color="text.secondary"
                sx={{ display: 'block', mb: 0.5, fontWeight: 'bold' }}
              >
                
              </Typography>
              {reasoning.map((step, index) => (
                <Typography
                  key={index}
                  variant="caption"
                  color="text.secondary"
                  sx={{ display: 'block', mb: 0.5 }}
                >
                  {step}
                </Typography>
              ))}
            </Box>
          )}

          {/*  */}
          <Typography variant="body1">{message}</Typography>
        </Paper>
      </Box>
    </Box>
  );
};

export default ChatBubble;
