import React, { useState } from 'react';
import { Container, Paper, Typography, Box } from '@mui/material';
import ChatWindow from './components/ChatWindow';
import MessageInput from './components/MessageInput';

const App = () => {
  const [messages, setMessages] = useState([
    { id: 1, text: '', isUser: false },
  ]);

  const handleSendMessage = (messageText) => {
    if (!messageText.trim()) return;

    // 1. 
    const userMsgId = Date.now();
    const userMessage = {
      id: userMsgId,
      text: messageText,
      isUser: true,
    };
    setMessages((prev) => [...prev, userMessage]);

    // 2. AI AI ...
    const aiMsgId = `${userMsgId}-ai`;
    const startTime = Date.now();
    const newAiMessage = {
      id: aiMsgId,
      text: '',          // 
      isUser: false,
      reasoning: [], // 
      thinkingTime: null,
      finalized: false,  // 
    };
    setMessages((prev) => [...prev, newAiMessage]);

    // 3. 2s, 4s, 6s, 8s
    setTimeout(() => {
      console.log('[AI] 1...');
      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === aiMsgId
            ? {
                ...msg,
                reasoning: [
                  ...msg.reasoning,
                  '...',
                ],
              }
            : msg
        )
      );
    }, 2000);

    setTimeout(() => {
      console.log('[AI] 2...');
      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === aiMsgId
            ? {
                ...msg,
                reasoning: [
                  ...msg.reasoning,
                  '...',
                ],
              }
            : msg
        )
      );
    }, 4000);

    setTimeout(() => {
      console.log('[AI] 3...');
      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === aiMsgId
            ? {
                ...msg,
                reasoning: [
                  ...msg.reasoning,
                  '...',
                ],
              }
            : msg
        )
      );
    }, 6000);

    // 4.  8 
    setTimeout(() => {
      console.log('[AI] ');
      const endTime = Date.now();
      const thinkingTime = ((endTime - startTime) / 1000).toFixed(1);

      setMessages((prev) =>
        prev.map((msg) =>
          msg.id === aiMsgId
            ? {
                ...msg,
                text: ` AI : ${messageText}`,
                thinkingTime,
                finalized: true, // 
              }
            : msg
        )
      );
    }, 8000);
  };

  return (
    <Box
      sx={{
        width: '100vw',
        height: '100vh',
        bgcolor: '#fafafa',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <Container maxWidth="md">
        <Paper
          elevation={3}
          sx={{
            p: 2,
            height: '80vh',
            display: 'flex',
            flexDirection: 'column',
          }}
        >
          <Typography variant="h5" gutterBottom>
            
          </Typography>
          <Box
            sx={{
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              overflow: 'hidden',
            }}
          >
            <ChatWindow messages={messages} />
          </Box>
          <MessageInput onSend={handleSendMessage} />
        </Paper>
      </Container>
    </Box>
  );
};

export default App;
